import React, {Component} from 'react';
import './HomeMobile.css';
import Draggable from 'react-draggable';
import {Link} from 'react-router-dom';


	 class HomeMobile extends Component {

		 render() {


				return (
                    <React.Fragment>
                        <img className="mobileHomeImage" src={require("./TableMobile.png")} alt=""/>
                        <div style={{display: "table"}}>
                            <Draggable>
                                <div className=" toolMobile blogMobile"> <Link to="/ideas"> <div className="fakeNavMobile"> </div> </Link> </div>
                            </Draggable>
                            <Draggable>
                                <div className=" toolMobile teachingMobile"> <Link to="/teaching"> <div className="fakeNavMobile"> </div> </Link> </div>
                             </Draggable>
                             <Draggable>
                                <div className=" toolMobile aboutMobile"> <Link to="/about"> <div className="fakeNavMobile"> </div> </Link> </div>
                             </Draggable>
                             <div className="toolMobile"> </div>
                             <Draggable>
                                <div className=" toolMobile projectsMobile"> <Link to="/projects"> <div className="fakeNavMobile"> </div> </Link> </div>
                             </Draggable>
                             <Draggable>
                                <div className=" toolMobile contactMobile"> <Link to="/contact"> <div className="fakeNavMobile"> </div> </Link> </div>
                             </Draggable>
                            <Draggable>
                                <div className=" toolMobile awardsMobile"> <Link to="/awards"> <div className="fakeNavMobile"> </div> </Link> </div>
                             </Draggable>
                         </div>
                    </React.Fragment>
				);


		 }
	 }

	export default HomeMobile;
