import React, {Component} from 'react';
import Carousel from 'react-bootstrap/Carousel';
import './AwardsMobile.css';

	 class Awards extends Component {
		//projects

		 render() {

               //Award:1
		                var mainAwardName= "Eagle All Around";
                        var mainAwardImage= "dalat.jpg";
                        var mainAwardContent= "The Eagle All Around award is the final major award presented by Dalat International School. The winner of this award must have a positive impact on the school in multiple ways, excel in and have earned an award in four major award areas (academics, sports, fine arts, and service), must show leadership in the school, and must show outstanding personal character. ";
                        var mainAwardMoreInfo="EagleAllAroundMoreInfo.pdf";

                //Award:2
                        var secondaryAwardName= "EARCOS Global Citizenship Award";
                        var secondaryAwardImage= "earcos.jpg";
                        var secondaryAwardContent= 'The East Asia Regional Council of Schools is an organization of 174 member schools in East Asia. The Global Citizenship Award is given to a student who "embraces the qualities of a global citizen." These qualities include a respect for the diversity of other nations, open-minded awareness, and effective communication abilities.';
                        var secondaryAwardMoreInfo="https://www.earcos.org/other_award.php";

                        var testimonial1Person = "Karl Steinkamp, Dalat School Director"
                        var testimonial1 = "If Einstein had an apprentice it would have been Andrew Kim. They would have enjoyed each other’s company, wrestling with questions pertaining to how the world works with the same vibrant curious nature. I use the term vibrantas it best represents Andrew’s questioning of everything. He inquires with a passion that goes beyond just understanding but a desire to master. He asks multidimensional questions about the simplest of things.  "
				return (
					 <React.Fragment>
					    <div className="testimonials">
					        <Carousel>
                              <Carousel.Item>
                                <div>
                                    <img
                                      className="d-block w-100"
                                      src={require("./steinkamp.png")}
                                      alt="First slide"
                                    />
                                    <Carousel.Caption>
                                          <div className="testimonialText">
                                            <h3> {testimonial1Person} </h3>
                                            <p> {testimonial1} </p>
                                          </div>
                                    </Carousel.Caption>
                                </div>
                              </Carousel.Item>
                              <Carousel.Item>
                                <div>
                                    <img
                                      className="d-block w-100"
                                      src={require("./steinkamp.png")}
                                      alt="First slide"
                                    />
                                    <Carousel.Caption>
                                          <div className="testimonialText">
                                            <h3> {testimonial1Person} </h3>
                                            <p> {testimonial1} </p>
                                          </div>
                                    </Carousel.Caption>
                                </div>
                              </Carousel.Item>
                              <Carousel.Item>
                                <div>
                                    <img
                                      className="d-block w-100"
                                      src={require("./steinkamp.png")}
                                      alt="First slide"
                                    />
                                    <Carousel.Caption>
                                          <div className="testimonialText">
                                            <h3> {testimonial1Person} </h3>
                                            <p> {testimonial1} </p>
                                          </div>
                                    </Carousel.Caption>
                                </div>
                              </Carousel.Item>
                            </Carousel>
					    </div>

                        <div className="container">
                            <h1 className="contentHeader">{mainAwardName} </h1>
                            <div className="projectsHolder">
                                <img src={require('./' + mainAwardImage)} alt="mainProjectImage"/>
                                <p className="contentText"> {mainAwardContent} <br/><br/><h4><a href="{mainAwardMoreInfo}"> ... More Info ...</a></h4></p>
                            </div>
                            <h1 className="contentHeader">{secondaryAwardName} </h1>
                            <div className="projectsHolder">
                                <img src={require('./' + secondaryAwardImage)} alt="mainResearchImage"/>
                                <p className="contentText"> {secondaryAwardContent} <br/><br/><h4><a href={secondaryAwardMoreInfo}> ... More Info ... </a></h4></p>
                            </div>

                         </div>
                     </React.Fragment>
				);
		 }
	 }

	export default Awards;