import React, {Component} from 'react';
import Carousel from 'react-bootstrap/Carousel';
import './About.css';



	 class About extends Component {

		 render() {
				return (
					 <React.Fragment>
                        <div className="aboutHolder">
                            <Carousel interval={null}>
                              <Carousel.Item>
                                <img
                                  className="d-block w-100"
                                  src={require("./casualPortrait.jpg")}
                                  alt="First slide"
                                />
                              </Carousel.Item>
                              <Carousel.Item>
                                <img
                                  className="d-block w-100"
                                  src={require("./professionalPortrait.jpg")}
                                  alt="Third slide"
                                />
                              </Carousel.Item>
                              <Carousel.Item>
                                <img
                                  className="d-block w-100"
                                  src={require("./semi-professional photo.jpg")}
                                  alt="Third slide"
                                />
                              </Carousel.Item>
                            </Carousel>

                            <div className="information">
                                <p>
                                    I am a Chemical Engineer studying at The Cooper Union in New York City. I have also studied computer science and I am proficient in computer aided design.
                                    I have lived in three countries and can speak their languages. Unlike most engineers, my interdisciplinary and intercultural background allows me to view a
                                    problem from many angles and solve problems in ways no one else could imagine.
                                    <br/><br/>
                                    As an engineer, I am fueled by my passion for energy sustainability and storage. Why haven't we mastered energy efficiency yet? My heart aches thinking back
                                    to my childhood in Malaysia, a third world country where trucks spew black clouds into the air and plastic tumbleweeds roam. I have a burning desire to bring
                                    energy efficiency to the developing countries that need it most.
                                    <br/><br/>
                                    My focus in energy saving and efficiency lead to research in converting common food wastes like coffee grinds into reusable energy in a carbon negative BECCS process.
                                    I am also the first inventor of a patent pending device that saves thousands of kilowatts in air conditioning and heating by improving air circulation within a home.
                                </p>
                                </div>
                            </div>
					 </React.Fragment>
				);
		 }
	 }


	export default About;
