import React, {Component} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEnvelope } from '@fortawesome/free-solid-svg-icons';
import { faPhone } from '@fortawesome/free-solid-svg-icons';
import { faLinkedin } from '@fortawesome/free-brands-svg-icons';
import { faFacebookMessenger } from '@fortawesome/free-brands-svg-icons';
import { faGithub } from '@fortawesome/free-brands-svg-icons';

import './Contact.css'


	 class Contact extends Component {
		 state = {

		 };

		 render() {
				return (
                    <React.Fragment>
                    <div className="extraSpace"> </div>
                    <div className="contactContainer">
                         <ul className="list-inline">
                             <li id="email"><FontAwesomeIcon className="list-inline-item contactIcon" icon={faEnvelope} style={{color: "27ADE4"}}/></li>
                                <div className="bannerContainer" id="emailBanner">
                                    <a href="mailto:akakimmykim@gmail.com"> akakimmykim@gmail.com </a>
                                    <img className="CODE" src={require("./gmailQRCode.svg")} alt="Gmail QR Code" />
                                </div>
                             <li id="phone"><FontAwesomeIcon className="list-inline-item contactIcon"  icon={faPhone} style={{color: "5FBB3F"}}/></li>
                                <div className="bannerContainer" id="phoneBanner">
                                    (+1) 917-442-7893
                                    <img className="CODE" src={require("./WhatsappQRCode.svg")} alt="WhatsApp QR Code" />
                                 </div>
                             <li id="linked"><FontAwesomeIcon className="list-inline-item contactIcon"  icon={faLinkedin} style={{color: "0070AC"}} /></li>
                                <div className="bannerContainer" id="linkedBanner">
                                    <a href="https://www.linkedin.com/in/therealandrewkim/"> www.linkedin.com/ </a>
                                    <img className="CODE" src={require("./linkedInQRCode.svg")} alt="LinkedIn QR Code" />
                                 </div>
                             <li id="messenger"><FontAwesomeIcon className="list-inline-item contactIcon"  icon={faFacebookMessenger} style={{color: "00AEFF"}} /></li>
                                <div className="bannerContainer" id="messengerBanner">
                                    <a href="https://www.m.me/andrew.kim.13569"> m.me/andrew.kim.13569 </a>
                                    <img className="CODE" src={require("./messengerQRCode.svg")} alt="Facebook Messenger QR Code" />
                                 </div>
                             <li id="github"><FontAwesomeIcon className="list-inline-item contactIcon"  icon={faGithub} /></li>
                                 <div className="bannerContainer" id="githubBanner">
                                    <a href="https://github.com/akakimmykim"> https://github.com/akakimmykim </a>
                                    <img className="CODE" src={require("./GithubQRCode.svg")} alt="GitHub QR Code" />
                                 </div>
                         </ul>
                     </div>
					 </React.Fragment>
				);
		 }
	 }

	export default Contact;
