import React, {Component} from 'react';
import "./Projects.css";
import {Link} from 'react-router-dom';

	 class Projects extends Component {
		 render() {

                //projects
                        var mainProjectName= "DAN the Fan";
                        var mainProjectImage= "DANIME.gif";
                        var mainProjectContent= "DAN is a fan that promotes air circulation between two rooms even when the door is closed, saving thousands of kilowatts in heating and cooling. Often, rooms are either too hot or too cold even when the air conditioner or heater is on, and those rooms will never change temperature when the doors are closed. DAN is a thin fan the height of standard door frames that slides in and out of place. DAN extends the door frame so that the door closes onto DAN as if it was closing onto the door frame. DAN improves air circulation between rooms so that any room can be comfortable, whether the door is open or closed.";
                        var mainProjectExtra= "DAN is Patent Pending"
                        var secondaryProjectName = "Personal Website";
                        var secondaryProjectImage= "Logo 500px.jpg";
                        var secondaryProjectContent ="Who needs Wix or Wordpress? Even as a Chemical Engineer, I am involved with Computer Science. With React as the library/framework for this website and CSS for major styling, I designed and coded this website.";

                //Research
                        var mainResearchName= "Alternative BECCS Process";
                        var mainResearchImage= "alternativeBECCS.JPG";
                        var mainResearchContent= "Is it feasible to get energy from common bio-waste in a carbon negative process? Doing research with Professor Amanda Simson in the Cooper Union, we pyrolized common food waste like walnut shells. The newly made biochar was then reacted in a high temperature reaction called gassification in a TGA to see its reactivity. The results show ...";
                        var secondaryResearchName = "Metal Organic Frameworks Simulations";
                        var secondaryResearchImage = "metalorganicframeworks.JPG";
                        var secondaryResearchContent = "The research focuses on studying the behavior of MOFs(Metal Organic Frameworks) used in optoelectronic device. By differing the structure of particles, chemical and physical behaviors of various combinations of inorganic metal joints and organic carbon links were studied in order to efficiency of the optoelectronic device. Computational physics and chemistry such as Gaussian and ChemCraft have been employed to figure out the stability and efficiency of the MOF particles. Avogadro was used to optimize the movement of elements and find potential maps within particles. Thermodynamic stability, VSEPR principles and modeling techniques are also used to calculate optimization energy of the various linkers and joints of the MOFs. Research shows the high electro potential and optimized enthalpy in Zr based MOFs are observed.";
				return (
                    <React.Fragment>
                        <div className="container">
                            <h1 className="contentHeader">{mainProjectName} </h1>
                            <div className="projectsHolder">
                                <img src={require('./' + mainProjectImage)} alt="mainProjectImage"/>
                                <p className="contentText"> {mainProjectContent} <br/><br/><h4> {mainProjectExtra} <br/><br/> <Link> ... More Info ...</Link> </h4> </p>
                            </div>
                            <h1 className="contentHeader">{mainResearchName} </h1>
                            <div className="projectsHolder">
                                <img src={require('./' + mainResearchImage)} alt="mainResearchImage"/>
                                <p className="contentText"> {mainResearchContent} <br/><br/><h4><Link> ... More Info ... </Link></h4></p>
                            </div>
                            <h1 className="contentHeader">{secondaryProjectName} </h1>
                            <div className="projectsHolder">
                                <img src={require('./' + secondaryProjectImage)} alt="mainProjectImage"/>
                                <p className="contentText"> {secondaryProjectContent} <br/><br/><h4><Link> ... More Info ... </Link></h4></p>
                            </div>
                            <h1 className="contentHeader">{secondaryResearchName} </h1>
                            <div className="projectsHolder">
                                <img src={require('./' + secondaryResearchImage)} alt="mainResearchImage"/>
                                <p className="contentText"> {secondaryResearchContent} <br/><br/><h4><Link> ... More Info ... </Link></h4></p>
                            </div>
                         </div>

                    </React.Fragment>
				);
		 }
	 }

	export default Projects;
