import React, {Component} from 'react';
import './teaching.css';


	 class Teaching extends Component {

		 render() {
				return (
				<React.Fragment>
				    <div className="teachingContainer">
                        <blockquote className="blockquote text-center">
                            <p> <b>Everybody is a Genius.</b><br/> But if you judge a fish by its ability to climb a tree, it will live its whole life believing that it is stupid.</p>
                            <footer className="text-right"><h4> - Albert Einstein </h4></footer>
                        </blockquote>

                             <div className="teachingPhilosophy">
                                 <img src={require("./fishTree.gif")} alt="fish climbing tree"/>

                                 <p> <h4> Even a fish <b> can </b> climb a tree. </h4> <br/><br/>I believe that everyone has the capacity to succeed in anything they want to, but every student has a different way to get to the top. As a teacher, I strive to bring forth a <b> passion </b> to learn
                                    by making the material <b> relatable </b> and <b> enjoyable</b>. I am always <b> available </b> online to answer questions or even to just have
                                    a friendly conversation. Because every student is unique, my teaching <b> evolves </b> so that they can comfortably understand the content. </p>

                             </div>
                     </div>

                        <div className="subjectContainer">
                             <div className="row">
                                <div className="cols">
                                    <div className="subjectBox">
                                        <h2 className="subjectTitle"> English </h2>
                                        <img  src={require("./english.gif")} alt="subjectImage"/>
                                        <p className="ageDescription">  English as a <br/><b> Second Language? First Language?</b> <br/>No Problem. </p>
                                    </div>
                                </div>
                                <div className="cols">
                                    <div className="subjectBox">
                                        <h2 className="subjectTitle"> Chemistry </h2>
                                        <img src={require("./chemistry.gif")} alt="subjectImage"/>
                                        <p className="ageDescription"> Why does Chemistry <b>matter</b>? Well...<br/> Chemistry is the study of <b>matter</b> </p>
                                     </div>
                                </div>
                                <div className="cols">
                                    <div className="subjectBox">
                                        <h2 className="subjectTitle"> Study Skills </h2>
                                        <img  src={require("./study.gif")} alt="subjectImage"/>
                                        <p className="ageDescription"> Memorize <b>Faster</b>. <br/> Understand <b>Deeper</b> <br/> Explain <b>Clearer</b>.</p>
                                     </div>
                                </div>
                             </div>
                                <div class="w-100"></div>
                             <div className="row">
                                <div className="cols">
                                    <div className="subjectBox">
                                        <h2 className="subjectTitle"> Math </h2>
                                        <img src={require("./math.gif")} alt="subjectImage"/>
                                        <p className="ageDescription"> Ever since elementary school, the math just keeps <b> adding up. </b> </p>
                                     </div>
                                </div>
                                <div className="cols">
                                    <div className="subjectBox">
                                        <h2 className="subjectTitle"> Biology </h2>
                                        <img src={require("./biology.gif")} alt="subjectImage"/>
                                        <p className="ageDescription"> The study of life. Now I feel <b> alive</b>. </p>
                                     </div>
                                </div>
                                <div className="cols">
                                     <div className="subjectBox">
                                        <h2 className="subjectTitle"> Testing </h2>
                                        <img src={require("./sat.gif")} alt="subjectImage"/>
                                        <p className="ageDescription"> What could be better than the <b> ACT </b> of studying on a <b> SAT</b>urday? </p>
                                     </div>
                                </div>
                             </div>
                         </div>


                </React.Fragment>
				);
		 }
	 }

	export default Teaching;
