import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import './NavMobile.css';

	 class NavMobile extends Component {
        constructor(props) {
            super(props);
            this.state = {
              menu: false
            };
            this.toggleMenu = this.toggleMenu.bind(this);
          }

          toggleMenu(){
            this.setState({ menu: !this.state.menu })
          }

          closeMenu () {
            this.setState({menu: false})
          }

		 render() {
		    const show = (this.state.menu) ? "show" : "" ;

				return (
					 <React.Fragment>
                        <nav className="navbar mobileNav fixed-top navbar-light">
                          <div><Link onClick={() => this.closeMenu()} to="/"><img className="navLogo" src={require("./Logo 400px.png")} alt="logo" /></Link></div>
                          <Link onClick={() => this.closeMenu()} to="/"><img className="navLogo" src={require("./signature.jpg")} alt="logo" /></Link>
                          <button className="navbar-toggler" type="button" onClick={ this.toggleMenu }>
                            <span className="navbar-toggler-icon"></span>
                          </button>
                          <div className={"collapse navbar-collapse " + show}>
                            <div className="navbar-nav">
                                    <Link onClick={() => this.closeMenu()} to="/projects">
                                        <li className="nav-item hvr-underline-from-center">Portfolio</li>
                                    </Link>
                                    <Link onClick={() => this.closeMenu()} to="/teaching">
                                        <li className="nav-item hvr-underline-from-center">Teaching</li>
                                    </Link>
                                    <Link onClick={() => this.closeMenu()} to="/awards">
                                        <li className="nav-item hvr-underline-from-center">Recognition</li>
                                    </Link>
                                    <Link onClick={() => this.closeMenu()} to="/about">
                                        <li className="nav-item hvr-underline-from-center">About</li>
                                    </Link>
                                    <Link onClick={() => this.closeMenu()} to="/ideas">
                                        <li className="nav-item hvr-underline-from-center">Ideas</li>
                                    </Link>
                                    <Link onClick={() => this.closeMenu()} to="/contact">
                                        <li className="nav-item hvr-underline-from-center">Contact</li>
                                    </Link>
                            </div>
                          </div>
                        </nav>
                        <div style={{height:"12vh", width:"100%"}}> </div>
                      </React.Fragment>
				);
		 }
	 }

	export default NavMobile;
