import React, {Component} from 'react';
import {Link} from 'react-router-dom';

	 class Blog extends Component {

		 render() {

		                var IdeaName= "Adjustable Barbed Bolt";
                        var IdeaImage= "adjustable barbed bolt.JPG";
                        var IdeaContent= "This device solves the issue destroyed screw threading, nail's weakness, and inaccessible screws. The adjustable barbed bolt is a temporary fastner that is easily implemented to hold structures in place. The tool is suited for jobs that require constant adjustment or jobs that require frequent build-ups and take-downs such as sporting nets and theatrical props.";

				return (
					 <React.Fragment>
                        <div className="container">
                            <h1 className="contentHeader">{IdeaName} </h1>
                            <div className="projectsHolder">
                                <img src={require('./' + IdeaImage)} alt="mainProjectImage"/>
                                <p className="contentText"> {IdeaContent} <br/><br/><h4> <br/><br/> <Link to="https://www.earcos.org/other_award.php"> ... More Info ...</Link> </h4> </p>
                            </div>
                         </div>
                      </React.Fragment>
				);
		 }
	 }

	export default Blog;
